body { color: #fff; font-size: 14px; line-height: 1.6; font-family: "Open Sans", sans-serif; }

a { display: block; color: #e5bb31; }
a:hover { text-decoration: none; }

h1 { font-size: 64px; }

h2 { font-size: 38px; }

h3 { font-size: 22px; }

h4 { font-size: 18px; }

.title { text-transform: uppercase; }
.title span { color: #e5bb31; }

.header { position: absolute; width: 100%; }
.header__wrap { background: #e5bb31; }

.footer { padding: 35px 0 40px; background: #000; }
.footer__title { margin-bottom: 30px; }
.footer__row { margin-top: 20px; *zoom: 1; }
.footer__row:after { content: " "; display: table; clear: both; }
.footer__row:first-child { margin-top: 0; }
.footer__img { float: left; margin-right: 8px; width: 50px; height: 50px; }
.footer__text { font-size: 12px; line-height: 1.4; color: #8a8a8a; }
.footer__link { display: inline-block; }

.btn { display: inline-block; padding: 0 50px; height: 50px; line-height: 50px; border: 1px solid #9b9b9b; font-size: 16px; color: #fff; text-transform: uppercase; text-decoration: none; transition: all 0.3s ease; }
.btn:hover { background: #e5bb31; }

.ico { display: inline-block; }
.ico.is-twitter { width: 18px; height: 14px; background-image: url("../img/Twitter.png"); }
.ico.is-facebook { width: 8px; height: 14px; background-image: url("../img/Facebook.png"); }
.ico.is-google { width: 16px; height: 14px; background-image: url("../img/Google.png"); }
.ico.is-pinterest { width: 12px; height: 14px; background-image: url("../img/Pinterest.png"); }

.social { margin-top: 5px; *zoom: 1; }
.social:after { content: " "; display: table; clear: both; }
.social__el { float: left; }
.social__el:first-child { margin-left: 0; }
.social__link { width: 25px; height: 25px; line-height: 30px; text-align: center; transition: all 0.3s ease; }
.social__link:hover { background-color: #e5bb31; }

.nav { float: right; }
.nav__list { *zoom: 1; }
.nav__list:after { content: " "; display: table; clear: both; }
.nav__el { float: left; }
.nav__link { padding: 0 20px; height: 100px; line-height: 100px; color: #fff; text-decoration: none; text-transform: uppercase; font-size: 16px; font-weight: 100; transition: all 0.3s ease; }
.nav__link:hover { background-color: #ebca5e; }

.top-banner { height: 700px; padding-top: 280px; background-color: rgba(0, 0, 0, 0.6); text-align: center; box-sizing: border-box; }
.top-banner__title { margin-bottom: 40px; font-weight: 100; }
.top-banner__text { max-width: 625px; margin: 0 auto 80px; line-height: 1.6; }

.privilege { padding: 100px 0; color: #3e3e3e; }
.privilege__title { font-weight: bold; }
.privilege__text { color: #3e3e3e; }
.privilege__link { display: inline-block; }

.help { padding: 50px 0; background-color: rgba(0, 0, 0, 0.6); text-align: center; }
.help__title { margin-bottom: 60px; font-weight: 100; }
.help__ico { margin-bottom: 20px; opacity: 0.7; }
.help__ico svg { fill: #fff; }

.projects { padding: 100px 0; color: #3e3e3e; *zoom: 1; }
.projects:after { content: " "; display: table; clear: both; }
.projects__title { margin-bottom: 30px; }

.owl-controls { position: absolute; top: -55px; right: 0; }

.owl-prev, .owl-next { display: inline-flex; }

.owl-next { margin-left: 10px; }

.carousel__img { width: 100%; height: 130px; background-color: #e5bb31; background-size: cover; background-position: top center; background-image: url("http://placehold.it/235x130"); }
.carousel__text { margin-top: 10px; }

.arrow { position: relative; width: 12px; height: 17px; }
.arrow.is-left:before, .arrow.is-right:before { position: absolute; content: ""; top: 4px; left: 0; width: 12px; height: 3px; background-color: #a1a1a1; }
.arrow.is-left:after, .arrow.is-right:after { position: absolute; content: ""; top: 11px; left: 0; width: 12px; height: 3px; background-color: #a1a1a1; }
.arrow.is-left:hover:after, .arrow.is-left:hover:before, .arrow.is-right:hover:after, .arrow.is-right:hover:before { background-color: #e5bb31; }
.arrow.is-left:before { transform: rotate(-45deg); }
.arrow.is-left:after { transform: rotate(45deg); }
.arrow.is-right:before { transform: rotate(45deg); }
.arrow.is-right:after { transform: rotate(-45deg); }


